.popup{
  position: fixed;
  top: 0;
  left:0;

  width: 100%;
  height: 100%;

  z-index: -1;
  opacity: 0;
  &.active{
    z-index: 10;

    opacity: 1;
  }
  &__wrapper{
    width: 100%;
    height: 100%;

    background-color: rgba(#000,.81);

    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__content{
    max-width: 920px;
    width: 100%;
  }

  &-close{
    &:hover{
      cursor: pointer;
    }
  }
}

.consult{
  width: 100%;
  &__wrapper{
    width: 100%;

    background-color: #036358;

    padding: 15px;
  }
  &__box{
    padding: 60px 30px;
    border: 1px solid #fff;

    position: relative;
  }

  &__header{
    padding-bottom: 30px;
    position: relative;

    &::after{
      content: '';
      position: absolute;
      left: calc(50% - 200px);
      bottom: 0;
      width: 400px;
      height: 1px;
      display: block;

      background-color: #fff;
    }
  }

  &__title{
    font-family: Georgia,sans-serif;
    font-weight: 700;
    font-size: 36px;

    color:#fff;

    text-transform: uppercase;
    text-align: center;
  }
  &__desc{
    margin: 40px auto 0 auto;

    max-width: 425px;
    width: 100%;

    text-align: center;

    font-family: 'Gilroy', sans-serif;
    font-weight: 300;

    color:#fff;
  }

  &__content{
    margin-top: 30px;
  }
  &__close{
    position: absolute;
    top: 15px;
    right: 15px;

    width: 20px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    &::before{
      content: '+';
      font-family: 'Gilroy', sans-serif;
      font-weight: 300;
      font-size: 48px;

      color:#fff;

      text-align: center;
      transform: rotate(45deg);
    }
  }

  &-form{
    &__content{
      display: grid;
      grid-template-columns: repeat(2,minmax(0,300px));
      gap: 10px;
    }
    &__wrapper{
      display: flex;
      align-items: center;
      flex-direction: column;
    }
    &__input{
      -webkit-appearance: none;
      background-color: #fff;
      outline: none;
      border: none;

      width: 100%;
      height: 45px;

      padding: 0 15px;

      font-family: 'Gilroy', sans-serif;
      font-weight: 300;
      font-size: 15px;
    }
    &__footer{
      margin-top: 30px;
    }
    &__desc{
      font-family: 'Gilroy', sans-serif;
      font-weight: 300;
      font-size: 12px;

      color:#fff;

      a{
        text-decoration: underline;
      }
    }
    &__submit{
      margin: 20px auto 0 auto;
      &:hover{
        cursor: pointer;
      }
    }
  }
}

@media screen and (max-width: 500px) {
  .consult{
    &__title{
      font-size: 24px
    }
    &__header{
      padding-bottom: 15px;
      &::after{
        width: 80%;
        left: 10%;
      }
    }
    &__desc{
      margin: 20px auto 0 auto;
    }
    &__content{
      margin-top: 15px;
    }
    &-form{
      &__content{
        width: 100%;
        grid-template-columns: minmax(0,1fr);
      }
    }
  }
}