.work {
  &-grid {
    &__wrapper {
      display: grid;
      grid-template-columns: repeat(5, minmax(0, 1fr));
      grid-auto-rows: 225px;

      gap: 25px;
    }
  }

  &-box {
    height: 100%;
    &__wrapper{
      position: relative;
      display: block;

      height: 100%;
      &::before{
        content: '';
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;

        width: 100%;
        height: 100%;
        display: block;

        background-color: rgba(#000,.55);

        opacity: 0;

        transition: 0.5s opacity linear;
      }
      &:hover{
        cursor: pointer;
        &::before{
          opacity: 1;
        }
        .work-box__img{
          &::before,&::after{
            opacity: 1;
          }
          &::after{
            transform: rotate(45deg);
          }
        }
      }
    }
    &__img {
      width: 100%;
      height: 100%;

      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;

      &::after{
        content: '';
        position: absolute;
        left: calc(50% - 47px);
        top: calc(50% - 47px);
        z-index: 2;

        width: 94px;
        height: 94px;
        display: block;

        transform: rotate(0);

        background-color: #036358;

        opacity: 0;

        transition: .3s opacity, .5s transform;
      }
      &::before{
        content: '+';
        position: absolute;
        left: calc(50% - 30px);
        top: calc(50% - 30px);
        z-index: 3;

        width: 60px;
        height: 60px;
        display: block;

        font-family: 'Gilroy', sans-serif;
        font-size: 60px;
        color:#fff;

        text-align: center;

        opacity: 0;

        transition: .5s opacity;
      }
      &-item{
        width: 100%;
        height: 100%;

        object-fit: cover;
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .work{
    &-grid{
      &__wrapper{
        grid-template-columns: repeat(4,minmax(0,1fr));
      }
    }
  }
}

@media screen and (max-width: 800px) {
  .work{
    &-grid{
      &__wrapper{
        grid-template-columns: repeat(3,minmax(0,1fr));
        gap: 15px;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .work{
    &-grid{
      &__wrapper{
        grid-template-columns: repeat(2,minmax(0,1fr));
        grid-auto-rows: calc(100vh / 4);
      }
    }
  }
}