.section{
  overflow: hidden;
  &__wrapper{
    padding: 70px 0 70px 0;
    &-small{
      padding: 35px 0 35px 0;
    }
  }
  &__title{
    font-family: Georgia;
    font-size: 26px;
    line-height: 36px;
    text-align: center;

    text-transform: uppercase;
    &-left{
      text-align: left;
    }
    &-white{
      color:#fff;
    }
  }
  &__desc{
    font-family: 'Gilroy',sans-serif;
    font-weight: 300;

    font-size: 16px;

    margin-top: 40px;
    &-no-m{
      margin-top: 0;
    }
    &-small{
      margin-top: 20px;
    }
    &-justify{
      text-align: justify;
    }
    p + p {
      margin-top: 30px;
    }
  }

  &__content{
    margin-top: 50px;
  }

  &__footer{
    margin: 40px 0 0 0;
  }

  &-advantages{
    &__desc{
      text-align: center;

      max-width: 1040px;
      width: 100%;

      margin: 40px auto 0 auto;
    }
  }
  &-grey{
    background-color: #efefef;
  }
  &-grid{
    &__wrapper{
      display: grid;
      grid-template-columns: repeat(2,minmax(0,1fr));
      gap: 50px;
    }
    &__img{
      display: flex;
      align-items: center;
      justify-content: center;

      height: 360px;
      &-item{
        width: 100%;
        height: 100%;

        object-fit: cover;
      }
    }
  }
}

.map{
  &__wrapper{
    width: 100%;
    height: 500px;
  }
  &__item{
    width: 100%;
    height: 100%;
  }

}

@media screen and (max-width: 991px) {
  .section{
    &-grid{
      &__wrapper{
        grid-template-columns: minmax(0,1fr);
        gap: 20px;
      }
      &__item{
        order: 3;
        &:nth-child(1){
          order:2;
        }
        &:nth-child(2){
          order: 1;
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .section{
    &__wrapper{
      padding: 45px 0;
      &-small{
        padding: 30px 0;
      }
    }
    &__content{
      margin-top: 30px;
    }
    &__desc{
      margin-top: 20px;
    }
    &-grid{
      &__img{
        height: 100%;
      }
    }
  }

  .map{
    &__wrapper{
      height: 300px;
    }
  }
}