.news{
  &-grid{
    &__wrapper{
      display: grid;
      grid-template-columns: repeat(2,minmax(0,1fr));
      gap: 80px 40px;
    }
  }
  &-pagination{
    &__wrapper{
      display: flex;
      align-items: center;
    }
    &__item{
      width: 25px;
      height: 25px;

      display: flex;
      align-items: center;
      justify-content: center;

      border: 1px solid #b8b8b8;

      &-active{
        background-color: #036358;

        color:#fff;

        border-color: transparent;
      }
      & + &{
        margin-left: 5px;
      }
    }
    &__control{
      width: 70px;
      &-next{
        &::after{
          content: '';

          width: 12px;
          height: 10px;
          display: block;

          margin:2px 0 0 5px;
          transform: rotate(180deg);

          background: url("../img/icons/arrow-pagination.png") center center/contain no-repeat;
        }
      }
      &-prev{
        &::before{
          content: '';

          width: 12px;
          height: 10px;
          display: block;

          margin:2px 5px 0 0;

          background: url("../img/icons/arrow-pagination.png") center center/contain no-repeat;
        }
      }
    }
    &__label{
      font-family: 'Gilroy', sans-serif;
      font-weight: 900;
      font-size: 16px;

      color: inherit;

      height: 16px;
      display: block;
    }
  }
  &__wrapper{
    display: grid;
    grid-template-columns: 248px auto;
    gap: 25px;
    &-block{
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      flex-direction: column;
    }
  }
  &__img{
    width: 100%;
    height: 248px;
    &-item{
      width: 100%;
      height: 100%;

      object-fit: cover;
    }
  }
  &__title{
    font-family: 'Gilroy', sans-serif;
    font-weight: 900;
    font-size: 18px;
  }
  &__date{
    font-family: 'Gilroy', sans-serif;
    font-weight: 300;
    font-size: 13px;

    color:#7f7f7f;

    margin-top: 10px;
  }
  &__desc{
    font-family: 'Gilroy', sans-serif;
    font-weight: 300;
    font-size: 16px;

    text-align: justify;

    hyphens:auto;
  }
  &__button{
    width: 175px;

    transition: 0.5s background-color;
    &:hover{
      background-color: #a25e31;
    }
  }
}

@media screen and (max-width: 991px) {
  .news{
    &-grid{
      &__wrapper{
        grid-template-columns: minmax(0,1fr);
        gap: 30px;
      }
    }
  }
}

@media screen and (max-width: 500px) {
  .news{
    &__wrapper{
      grid-template-columns: minmax(0,1fr);
      gap: 30px;
    }
    &__img{
      width: 100%;
      height: 250px;
    }
    &__content{
      margin: 15px 0;
    }
  }
}