body{
  color:#212026;
}

.container{
  max-width: 1240px;
  width: 100%;


  padding: 0 15px;

  margin: 0 auto;
}

.page-wrapper{
  display: flex;
  flex-direction: column;

  min-height: 100vh;
}

.footer{
  margin-top: auto;
}

.button{
  -webkit-appearance: none;

  border: none;
  outline: none;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 264px;
  height: 50px;

  text-align: center;

  font-family: 'Gilroy', sans-serif;
  font-weight: 900;
  font-size: 16px;
  color:#fff;
  &-green{
    background-color: #036358;
  }
  &-yellow{
    background-color: #a25e31;
  }
  &-big{
    width: 310px;
  }
  &-center{
    margin: 0 auto;
  }
}

@media screen and (max-width: 350px) {
  .button{
    &-big{
      width: 100%;
    }
  }
}