.contacts{
  &__wrapper{
    display: grid;
    grid-template-columns: repeat(3,minmax(0,1fr));
    grid-auto-rows: 369px;
    gap: 30px;
    &-block{
      width: 100%;
      height: 100%;
    }
  }
  &__img{
    width: 100%;
    height: 100%;
    &-item{
      width: 100%;
      height: 100%;

      object-fit: cover;
    }
  }
  &__info{
    &-header{
      padding: 0 0 35px 0;
    }
    &-content{
      padding: 35px 0 0 0;

      border-top: 1px solid #e3e3e3;
    }
  }
}

@media screen and (max-width: 991px) {
  .contacts{
    &__wrapper{
      grid-template-columns: minmax(0,1fr);
      grid-auto-rows: auto;
      &-block{
        order: 2;
        &:nth-child(3){
          order: 1;
        }
      }
    }
    &__img{
      height: auto;
    }
    &__info{
      &-header{
        padding: 0 0 15px 0;
      }
      &-content{
        padding: 15px 0 0 0;
      }
    }
  }
}