.header {
  position: relative;
  z-index: 10;
  &__logo {
    width: 235px;
    height: 100%;
  }

  &__block {
    height: 100%;

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    &-content {
      padding: 25px 0 25px 0;
    }
  }

  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;

    height: 142px;
  }

  &__row {
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }

  &__phone {
    margin: 0 60px 0 65px;
  }

  &__burger{
    display: none;
  }

  &-second {
    box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.16);
  }
}

.logo {
  width: 100%;
  height: 100%;
  position: relative;

  &__img {
    width: 100%;
    height: 100%;
    object-fit: contain;

    &-big {
      height: 275px;

      position: absolute;
      top: 0;
      left: 0;

      object-position: top;
    }
  }
}

.mail {
  display: flex;
  align-items: center;

  &__icon {
    display: block;
    width: 20px;
    height: 15px;

    flex-shrink: 0;

    &-item {
      width: 100%;
      height: 100%;

      object-fit: contain;
    }
  }

  &__content {
    margin-left: 15px;
  }

  &__link {
    font-family: 'Gilroy', sans-serif;
    font-weight: 300;

    color: #212026;

    font-size: 16px;

    &-white {
      color: #fff;
    }
  }
}

.phone {
  display: flex;
  align-items: center;

  &__icon {
    display: block;
    width: 18px;
    height: 18px;

    flex-shrink: 0;

    &-item {
      width: 100%;
      height: 100%;

      object-fit: contain;
    }
  }

  &__content {
    margin-left: 15px;
  }

  &__link {
    font-family: 'Gilroy', sans-serif;
    font-weight: 900;

    font-size: 22px;

    color: #212026;

    &-white {
      color: #fff;
    }
  }
}

.address {
  display: flex;
  align-items: center;
  justify-content: center;

  &__icon {
    display: block;
    width: 12px;
    height: 20px;

    flex-shrink: 0;

    &-item {
      width: 100%;
      height: 100%;

      object-fit: contain;
    }
  }

  &__content {
    margin-left: 15px;
  }

  &__link {
    font-family: 'Gilroy', sans-serif;
    font-weight: 300;

    color: #212026;

    font-size: 16px;

    &-white {
      color: #fff;
    }
  }
}

.nav {
  width: 100%;

  &__list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;

    &-sub {
      position: absolute;
      left: 0;
      top: 46px;
      flex-direction: column;
      align-items: flex-start;

      padding: 35px 20px 35px 30px;
      background-color: #e4e4e4;

      min-width: 340px;

      display: none;
    }
  }

  &-list {
    &__item {
      position: relative;

      & + & {
        margin: 0 0 0 50px;
      }

      &:hover {
        &::after {
          content: '';
          width: 100%;
          height: 30px;

          background-color: transparent;

          position: absolute;
          bottom: -25px;
          left: 0;
        }

        .nav {
          &__list {
            &-sub {
              display: block;
            }
          }
        }
      }

      &-sub {
        display: flex;
        align-items: flex-start;

        & + & {
          margin: 15px 0 0 0;
        }

        &::before {
          content: '';
          width: 7px;
          height: 7px;
          display: block;

          border-radius: 50%;

          background-color: #a25e31;

          margin: 4px 10px 0 0;

          transition: .5s background-color;
        }

        &:hover {
          &::before {
            background-color: #036358;
          }
        }
      }
    }
  }

  &-item {
    &__link {
      font-family: 'Gilroy', sans-serif;
      font-weight: 300;
      color: #212026;
      text-transform: uppercase;

      transition: .5s color;

      &:hover {
        color: #036358;
      }

      &-small {
        text-transform: unset;
      }
    }
  }
}

.burger{
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;

  width: 40px;
  height: 20px;

  &__item{
    width: 100%;
    height: 2px;

    background-color: #161616;

    transition: 0.5s all linear;
  }

  &:hover{
    cursor: pointer;
  }

  &.active{
    position: relative;
    .burger{
      &__item{
        position: absolute;
        &:nth-child(1){
          top: 10px;
          transform: rotate(45deg);
        }
        &:nth-child(2){
          opacity: 0;
        }
        &:nth-child(3){
          top: 10px;
          transform: rotate(-45deg);
        }
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .header{
    &__phone{
      margin: 0 30px;
    }
  }
  .nav{
    &-list{
      &__item{
        & + &{
          margin:0 0 0 30px;
        }
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .header{
    &__block{
      &-content{
        position: fixed;
        z-index: 4;
        bottom: 0;
        right: 0;

        height: 100%;
        width: 100%;
        max-width: 300px;

        background-color: #fff;

        flex-direction: column-reverse;
        justify-content: center;

        padding: 15px;

        transform: translateX(100%);

        transition: 0.5s transform linear;
        &.active{
          transform: translateX(0);
        }
      }
    }
    &__mail{
      display: none;
    }
    &__phone{
      margin: 15px 0;
    }
    &__row{
      flex-direction: column;
    }
    &__burger{
      display: flex;
      align-items: center;
      justify-content: center;

      position: absolute;
      right: 15px;
      z-index: 5;
    }
  }
  .nav{
    &__list{
      flex-direction: column;
      align-items: flex-start;

      &-sub{
        position: relative;
        top: 0;
        left: 0;
        display: block;
        width: 100%;

        padding: 15px 5px 15px 15px;

        background-color: transparent;
      }
    }
    &-list{
      &__item + &__item{
        margin: 15px 0 0 0;
      }
      &__item{
        width: 100%;
        &-sub{
          max-width: 240px;
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .header{
    &__content{
      height: 100px;
    }
    &__logo{
      width: 100px;
    }
  }
}