.services{
  &-list{
    &__row{
      display: flex;
      align-items: center;
      justify-content: space-between;

      min-height: 50px;

      padding: 10px 40px;

      &-title{
        background-color: #036358;
      }

      & + &{
        border-bottom: 1px solid #d9d9d9;
      }
    }
    &__title{
      font-family: 'Gilroy', sans-serif;
      font-weight: 900;

      font-size: 18px;

      color:#fff;
    }
    &__text{
      font-family: 'Gilroy', sans-serif;
      font-weight: 300;

      font-size: 24px;

      color:#1d1c22;
    }
  }
}

@media screen and (max-width: 600px) {
  .services{
    &-list{
      &__row{
        padding: 10px 15px;
      }
      &__text{
        font-size: 16px;
      }
      &__block{
        & + &{
          margin-left: 30px;
        }
        &:nth-child(2){
          width: 100px;
          flex-shrink: 0;
        }
      }
    }
  }
}