.footer{
  &__wrapper{
    background-color: #161616;
  }
  &__row{
    height: 100px;

    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__text{
    font-family: 'Gilroy', sans-serif;
    font-weight: 300;

    font-size: 12px;
    color:#fff;
  }
  &__block{
    &-row{
      display: flex;
      align-items: center;
    }
  }
  &__mail{
    margin: 0 45px 0 75px;
  }
}

@media screen and (max-width: 1100px) {
  .footer{
    &__mail{
      margin: 0 15px;
    }
  }
}

@media screen and (max-width: 991px) {
  .footer{
    &__row{
      flex-direction: column-reverse;
      height: auto;

      padding: 30px 0;
    }
    &__text{
      text-align: center;
    }
    &__block{
      & + &{
        margin-bottom: 15px;
      }
    }
  }
}

@media screen and (max-width: 800px) {
  .footer{
    &__block{
      &-row{
        flex-direction: column-reverse;
      }
    }
    &__mail{
      margin: 15px 0;
    }
  }
}

@media screen and (max-width: 600px) {
  .footer{
    &__mail,&__address,&__phone{
      width: 240px;
    }
  }
}