.hero{
  &-slider{
    &__wrapper{
      display: flex;
      align-items: stretch;

      height: calc(100vh - 142px);
      min-height: 600px;

      position: relative;
    }
    &__item{
      height: 100%;
      width: calc(40% / 3);
      transition: 0.5s width linear;
      &--active{
        width: 70% !important;
        flex-shrink: 0;

        .hero-slider__item-img{
          &::before{
            opacity: 0;
          }
        }
        .hero-slider__item-content{
          opacity: 1;
        }
      }
      &-img{
        width: 100%;
        height: 100%;

        position: relative;

        &::before{
          content: '';
          position: absolute;
          left: 0;
          top: 0;

          width: 100%;
          height: 100%;
          display: block;

          background-color: rgba(#000,.5);

          opacity: 1;

          transition: .5s opacity;
        }

        img{
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      &-content{
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        z-index: 3;

        padding: 30px 0;

        opacity: 0;

        display: flex;
        align-items: flex-end;

        transition: 0.5s opacity;
      }
      &-info{
        width: 100%;
        max-width: 525px;
        background-color: rgba(#000,.55);

        padding: 55px 40px 30px 40px;

        &_end{
          margin-left: auto;
          margin-right: 140px;
        }
      }
      &-title{
        font-family: Georgia;
        font-size: 32px;
        text-transform: uppercase;
        color:#fff;
      }
      &-text{
        font-family: 'Gilroy', sans-serif;
        font-weight: 300;

        font-size: 16px;

        color:#fff;

        text-transform: uppercase;
      }
      &-header{
        padding-bottom: 25px;
        margin-bottom: 25px;

        border-bottom: 1px solid #fff;
      }
      &-footer{
        margin-top: 25px;
      }
      &-button{
        width: 175px;
      }
    }
    &__controls{
      position: absolute;
      z-index: 5;
      right: calc(50% - 620px);
      bottom: 30px;

      display: grid;
      grid-template-columns: repeat(2,minmax(0,60px));
      grid-auto-rows: 60px;
      gap: 10px;
    }
    &-button{
      &-next,&-prev{
        background-color: rgba(#000,.55);

        display: flex;
        align-items: center;
        justify-content: center;

        position: relative;

        &::before{
          content: '';

          width: 30px;
          height: 22px;

          display: block;

          background: url("../img/icons/hero-arrow.png") center center/contain no-repeat;

          transition: .5s opacity;
        }

        &:hover{
          cursor: pointer;
          &::before{
            opacity: .6;
          }
        }
      }
      &-prev{
        &::before{
          transform: rotate(180deg);
        }
      }
    }
  }
}

@media screen and (max-width: 1280px) {
  .hero{
    &-slider{
      &__controls{
        right: 15px;
      }
    }
  }
}

@media screen and (max-width: 700px) {
  .hero{
    &-slider{
      &__item{
        width: 0 !important;
        &--active{
          width: 100% !important;
        }
        &-content{
          align-items: center;
        }
        &-info{
          margin: 0 auto;
        }
      }
      &__controls{
        right: calc(50% - 65px);
        bottom: 15px;
      }
    }
  }
}