.advantages{
  &-grid{
    display: flex;
    align-items: center;
    justify-content: center;
    &__wrapper{
      display: grid;

      grid-template-columns: repeat(5,minmax(0,120px));
      gap: 70px;
    }
  }
  &-item{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    &__icon{
      width: 110px;
      height: 110px;

      &-img{
        width: 100%;
        height: 100%;

        border-radius: 50%;

        object-fit: cover;
      }
    }
    &__title{
      font-family: 'Gilroy', sans-serif;
      font-weight: 900;
      font-size: 16px;

      text-align: center;
      text-transform: uppercase;
    }
    &__content{
      margin-top: 20px;
    }
  }
}

@media screen and (max-width: 991px) {
  .advantages{
    &-grid{
      &__wrapper{
        gap: 30px;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .advantages{
    &-grid{
      &__wrapper{
        grid-template-columns: repeat(3,minmax(0,1fr));
      }
    }
  }
}

@media screen and (max-width: 500px) {
  .advantages{
    &-grid{
      &__wrapper{
        grid-template-columns: repeat(2,minmax(0,1fr));
      }
    }
  }
}