.partners{
  &-item{
    width: 100%;
    height: 143px;

    background-color: #dcdcdc;
    &__img{
      width: 100%;
      height: 100%;

      object-fit: cover;
    }
  }
  &-slider{
    &-wrapper{
      position: relative;

    }
    &-button{
      &-prev,&-next{
        position: absolute;
        top: calc(50% - 12px);
        width: 35px;
        height: 24px;

        display: block;

        background: url("../img/icons/arrow.png") center center/contain no-repeat;

        transition: .5s opacity;
        &:hover{
          cursor: pointer;

          opacity: .7;
        }

        &.swiper-button-disabled{
          opacity: .3;
        }
      }
      &-prev{
        left: -55px;

        transform: rotate(180deg);
      }
      &-next{
        right: -55px;
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .partners{
    &-slider{
      &-button{
        &-next,&-prev{
          display: none;
        }
      }
      .swiper-slide{
        width: 143px;
      }
    }
    &-item{
      width: 143px;
    }
  }
}