.section{
  &-banner{
    background: url("../img/banner.jpg") center center/cover no-repeat;
    &__desc{
      max-width: 580px;
    }
    &__button{
      width: 350px;
    }
  }
}

@media screen and (max-width: 475px) {
  .section{
    &-banner{
      background-position: -800px center;
      &__button{
        width: 100%;
      }
    }
  }
}